.scrollbar-wrapper {
  position: relative;
  overflow: hidden;
}

.scrollbar-wrapper .scrollbar {
  overflow: scroll;
  display: inline-block;
  right: -20px;
  width: calc(100% + 20px) !important;
  padding: 0 34px 0 0;
}

.scrollbar-wrapper .scrollbar + .scrollbar-handler {
  display: inline-block;
  width: 12px;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0, 1, 1, 1);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.scrollbar-wrapper .scrollbar + .scrollbar-handler:hover, .scrollbar-wrapper .scrollbar + .scrollbar-handler.scroll-active {
  opacity: 1;
}

.scrollbar-wrapper .scrollbar + .scrollbar-handler > .bar {
  width: 10px;
  margin: 0 1px;
  background: rgba(0, 0, 0, 0.3);
  opacity: .8;
  border-radius: 16px;
  overflow: hidden;
  transition: background .3s ease-in-out;
}

.scrollbar-wrapper .scrollbar + .scrollbar-handler > .bar:hover {
  opacity: 1;
}

.scrollbar-wrapper .scrollbar + .scrollbar-handler > .bar:active {
  opacity: 1;
  background: rgba(0, 0, 0, 0.5);
}

